.title {
  padding-top: 5px;
  text-align: center;
  font-size: xx-large;
  font-family: cursive;
  color: darkslateblue;
}

.content {
  position: relative;
  left: 25%;
  width: 50%;
}

.card {
  padding: 5px;
  margin: 5px;
  border: 0.2mm ridge rgb(50 98 220 / 60%);
}

.vocabulary {
  display: flex;
  align-items: center;
}

.word {
  padding-right: 5px;
}
.definition {
  margin-top: 0;
}

.example-button {
  display: inline-block;
  padding: 10px 20px;
  margin: 10p;
  font-size: 15px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: cornflowerblue;
  border: none;
  border-radius: 15px;
}

.button:active {
  background-color: cornflowerblue;
  box-shadow: 0 3px #666;
  transform: translateY(4px);
}

.icon-button {
  border: 0;
  padding: 0;
  background: white;
}

.fa-star {
  color: darkgray;
  padding: 5px;
}

.intro{
  height: 300px;
  color: rgb(35, 91, 140);
  float: right;
  width: 80%;
  font-size: 20px
}
